.sendForm {
    position: relative;
    margin-bottom: 35px;
    display: flex;
}

.textarea {

    text-align: left;
    padding: 10px;
    width: 100%;
    height: 40px !important;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    resize: none;
    overflow: auto;


}

.button {
    margin-left: 45px;
    margin-right: 70px;
    width: 120px;
    height: 40px;
    background: #0066CC;
    border-radius: 20px;
    color: #FFFFFF;
    border: none;

}

.button:hover {
    background-color: red;
}

.button:active {
    background-color: #00cc22;
}
